<template>
    <div class="max-group" v-loading="$store.state.loading">
        <h3>参演人员签到列表</h3>
        <div class="shouldMax">
            <p>
                <span>应到人数 {{ countMax.should }}</span>
                <span>已到人数<span style="color: #4a90e2;"> {{ countMax.already }}</span></span>
                <span>未到人数<span style="color: #d0021b;"> {{ countMax.should - countMax.already }}</span></span>
            </p>
            <div style="font-size: 16px;height: 25px;line-height:25px;margin-bottom: 10px;">参演者</div>
            <ul class="draw_list">
                <li v-if="item.character == 1" v-for="(item,index) in signMaxLists" :key="index" :class="item.user_status == '1' ? 'already_sign': ''">
                    <i class="iconfont el-icon-emergency-dagou1"></i>
                    <span>{{ item.user_name }}</span>
                    <span>{{ item.section_one }}{{ item.section_two }}</span>
                </li>
            </ul>
            <div style="font-size: 16px;height: 25px;line-height:25px;margin-bottom: 10px;">点评或评分人</div>
            <ul class="draw_list">
                <li v-if="item.character != 1" v-for="(item,index) in signMaxLists" :key="index" :class="item.user_status == '1' ? 'already_sign': ''">
                    <i class="iconfont el-icon-emergency-dagou1"></i>
                    <span>{{ item.user_name }}</span>
                    <span>{{ item.section_one }}{{ item.section_two }}</span>
                </li>
            </ul>
        </div>
        <h3  v-if="maxDrawResult">抽签结果</h3>
        <div class="draw_result" v-if="maxDrawResult">
            <ul>
                <li v-for="(item,key) in drawResultList" :key="key">
                    <div class="groupName">{{ item.name }}</div>
                    <div class="groupList">
                        <p v-for="(group,index) in item.user" :key="index">
                            <span>{{ group.user_name }}</span>
                            <span>{{ group.section_one }}{{ group.section_two }}</span>
                        </p>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
    	data(){
    		return{
				project_id: '',
				countMax: {
					should: '',
					already: '',
					not: '',
				},  //人数详情
                signMaxLists:[],     //签到人员列表详情（参演，评分点评）
                drawResultList: [],  //所有人的抽签结果的列表
				maxDrawResult:false,    //是否显示所有组抽签结果
                isMaxDraw: true,   //是否展示抽签（是否进行抽签）  不分组的时候不展示出来
                //定时器
				timeMaxDraw: '',        //签到列表的定时器
				timeStart: '',          //是否开始演练的定时器
            }
        },
        created(){
			this.$store.state.loading = true;
			this.project_id = localStorage['drill_project'];
			this.getDrawList(); //获取参演人员列表
			this.judgeDraw();   //获取抽签结果
        },
        mounted(){
			this.timeMaxDraw = setInterval(()=>{
				if(this.maxDrawResult){
					//显示抽签结果后 停止定时器
					clearInterval(this.timeMaxDraw);
				}
				this.getDrawList();     //查看签到详情
                if(this.isMaxDraw){
					this.judgeDraw();   //获取抽签结果
                }
			},5000);
			this.timeStart = setInterval(()=>{
				this.getMaxStart();
			},2000);
        },
        methods:{
        	//获取参演人员签到列表
			getDrawList(){
				let self = this;
				this.$axios.post('/api/user-sign',{
					design_id: this.project_id
				}).then(function (response) {
//                    console.log(response);
					//获取参演人员签到列表  去除导演等
					let showAll = []; //所有人  将对象数据转为数组（且取出点评人）
					let otherArr = [];  //所有除导演外的
					if(response.data.status == '0'){
						//最后一个键值  如果是score_user 有点评人
						let last = response.data.data["score_user"];
//						console.log(response.data.data["score_user"]);
//					    console.log(Object.keys(response.data.data)[Object.keys(response.data.data).length-1]);
						if(last.length == 0){ //没有点评人
							for(let i in response.data.data){
								showAll.push(response.data.data[i])
							}
						} else {
							//有点评人
							for(let i in response.data.data){
								showAll.push(response.data.data[i])
							}
							showAll.pop();    //去除点评数组后重新加入数据
							showAll.push(response.data.data["score_user"][0])
//							console.log(showAll)
						}
						for(let i in showAll){
							if(showAll[i].character != '0'){
								otherArr.push(showAll[i]);
							}
						}

//						for(let i in response.data.data){
//							if(response.data.data[i].character != '0'){
//								otherArr.push(response.data.data[i]);
//							}
//						}
					}
					self.signMaxLists = otherArr;//签到人员列表
					self.countMax.should = self.signMaxLists.length;
					let has = 0;
					for(let i in self.signMaxLists){
						if(self.signMaxLists[i].user_status == '1'){
							has++;
						}
					}
					self.countMax.already = has;
				}).catch(function () {
				})
            },
            //判断是否已经抽签，获取抽签结果
			judgeDraw(){
				this.$axios.post('/api/the-draw',{
					design_id: this.project_id
                }).then((response) => {
//                    console.log(response);
//                    console.log(response.data.data[0].user.length);
                    if(response.data.data){
						if(response.data.data[0].name == '此演练不分组'){
							this.$store.state.loading = false;
							//不分组  不显示抽签分组结果
                            this.maxDrawResult = false;
                            this.isMaxDraw = false;
                        } else {
							//分多组
							if(response.data.data[0].user.length == 0){
								//还没抽签分组
                                this.isMaxDraw = true;      //需要继续掉抽签接口
                                this.maxDrawResult = false; //不展示抽签结果
							} else {
								//获取分组抽签的数据 并显示
								let drawArr = response.data.data;
								for(let x in response.data.data){
									for(let y in response.data.data[x].user){
										drawArr[x].user[y].section_one = drawArr[x].user[y].section.section_one;
										drawArr[x].user[y].section_two = drawArr[x].user[y].section.section_two;
										drawArr[x].user[y].user_name = drawArr[x].user[y].name;
									}
								}
								this.drawResultList = drawArr;
                                this.maxDrawResult = true;   //展示抽签结果
								this.isMaxDraw = false;      //不需要继续掉抽签接口
							}
							this.$store.state.loading = false;
                        }
                    }
                }).catch((error)=>{})
            },
            //判断是否已经开始演练
            getMaxStart(){
                this.$axios.post('/api/look-status',{
                    "design_id":this.project_id
                }).then((response)=>{
//                    console.log(response)
                    if(response.data.data.design_location!==null){
                        this.$router.push('/maxplay');
                        clearInterval(this.timeStart)
                    }
                }).catch((error)=>{
                    console.log(error)
                })
            },
        },
    }
</script>

<style lang="less" rel="stylesheet/less">
    .max-group{
        width: 1200px;
        margin: 0 auto;
        padding-top: 50px;
        >h3{
            font-size: 24px;
            font-weight: normal;
            text-align: center;
            margin: 0 auto;
        }
        .shouldMax{
            //应到人数
            p{
                margin-bottom: 20px;
                span{
                    height: 22px;
                    font-size: 20px;
                    text-align: left;
                    color: #000000;
                }
                >span{
                    margin-right: 40px;
                }
            }
            //签到人员列表
            ul.draw_list{
                padding: 0;
                margin: 40px auto;
                li{
                    font-size: 18px;
                    text-align: left;
                    height: 22px;
                    line-height: 22px;
                    display: inline-block;
                    margin-bottom: 10px;
                    color: #9b9b9b;
                    width: 25%;
                    i{
                        color: #9b9b9b;
                        font-size: 20px;
                    }
                    span{
                        display: inline-block;
                        width: 90px;
                    }
                    span:last-child{
                        width: 170px;
                        text-align: left;
                        float: right;
                        height: 22px;
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 1;
                        overflow: hidden;
                    }
                }
                .already_sign{
                    color: #000000;
                    i{
                        color: #4a90e2;
                    }
                }
            }
        }
        //抽签结果
        .draw_result{
            margin-top: 14px;
            ul{
                padding: 0;
                li{
                    display: inline-block;
                    vertical-align: top;
                    background-color: #fff;
                    font-size: 20px;
                    line-height: 2.12;
                    margin-top: 10px;
                    padding: 14px 0 22px;
                    margin-right: 17px;
                    margin-bottom: 20px;
                    width: 30%;
                    .groupName{
                        text-align: center;
                        color: #4a4a4a;
                        font-weight: 500;
                    }
                    .groupList{
                        span{
                            text-align: left;
                            display: inline-block;
                            width: 70px;
                            margin-left: 30px;
                        }
                        span:last-child{
                            width: auto;
                            margin-left: 0;
                        }
                    }
                }
            }
        }
    }
</style>


